import React, { useState } from "react";
import SignIn from "./SignInContainer";
import SignUp from "./SignUpContainer";
import { Container, Wrapper, StateChanger, Link } from "./StyledComponents";

export default () => {
  const [action, setAction] = useState("login");
  return (
    <Container>
      <Wrapper>
        {action === "signup" ? <SignUp setAction={setAction} /> : <SignIn />}
        <StateChanger>
          {action === "login" && (
            <div>
               &nbsp;
              <Link onClick={() => setAction("signup")}></Link>
            </div>
          )}
          {action === "signup" && (
            <div>
               &nbsp;
              <Link onClick={() => setAction("login")}></Link>
            </div>
          )}
        </StateChanger>
      </Wrapper>
    </Container>
  );
};
