import React from "react";
import { Helmet } from "react-helmet";
import styled from "styled-components";
import { FormWrapper, Label, InputWrapper } from "./StyledComponents";
import Input from "../common/Input";
import Button from "../common/Button";
import Loader from "../common/Loader";
import { Thumbnail } from "../icon";

const UploadWrapper = styled.div`
  ${props => props.theme.whiteBox}
  width: 100%;
  height: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
  cursor: pointer;

  & img {
    width: 100%;
    height: 100%;
  }
`;

export default ({
  loading,
  nickname,
  email,
  pwd,
  confirmPwd,
  preview,
  fileEl,
  confirmPwdEl,
  handleChangePreview,
  handleChangeConfirmPwd,
  handleClickUpload,
  handleSubmit
}) => {
  return (
    <FormWrapper>
      <Helmet>
        <title></title>
      </Helmet>
      {loading && <Loader />}
      <form onSubmit={handleSubmit}>
        <UploadWrapper onClick={handleClickUpload}>
          {preview ? (
            <img src={preview} alt="avatar" title=" ." />
          ) : (
            <Thumbnail style={{ width: 100, height: 50 }} />
          )}

          <input
            type="file"
            onChange={handleChangePreview}
            ref={fileEl}
            hidden
            accept="image/jpg, image/jpeg, image/png"
          />
        </UploadWrapper>
        <InputWrapper>
          <Label htmlFor="email" val={email.value}>
            
          </Label>
          <Input type="email" placeholder="" name="email" {...email} />
        </InputWrapper>
        <InputWrapper>
          <Label htmlFor="password" val={pwd.value}>
            
          </Label>
          <Input type="password" placeholder="" name="password" {...pwd} />
        </InputWrapper>
        <InputWrapper>
          <Label htmlFor="conform_password" val={confirmPwd}>
             
          </Label>
          <Input
            type="password"
            placeholder=" "
            name="conform_password"
            value={confirmPwd}
            onChange={handleChangeConfirmPwd}
            ref={confirmPwdEl}
          />
        </InputWrapper>
        <InputWrapper>
          <Label htmlFor="nickname" val={nickname.value}>
            
          </Label>
          <Input placeholder="" name="nickname" {...nickname} />
        </InputWrapper>
        <Button text="" />
      </form>
    </FormWrapper>
  );
};
