import React from "react";
import { Helmet } from "react-helmet";
import { FormWrapper, InputWrapper, Label } from "./StyledComponents";
import Input from "../common/Input";
import Button from "../common/Button";
import Loader from "../common/Loader";

export default ({ loading, email, pwd, onSubmit }) => {
  return (
    <FormWrapper>
      <Helmet>
        <title></title>
      </Helmet>
      {loading && <Loader />}
      <form onSubmit={onSubmit}>
        <InputWrapper>
          <Label htmlFor="email" val={email.value}>
            
          </Label>
          <Input type="email" placeholder="" name="email" {...email} />
        </InputWrapper>
        <InputWrapper>
          <Label htmlFor="password" val={pwd.value}>
            
          </Label>
          <Input type="password" placeholder="" name="password" {...pwd} />
        </InputWrapper>
        <Button text={""} />
      </form>
    </FormWrapper>
  );
};
